@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');

:root {
  --primary-color: #303b47;
  --secondary-color: #66454f;
  --highlight: #666445;
  --white: white;
}

* {
  box-sizing: border-box;
}

body {
  background-image: url("./assets/images/AdobeStock_167882675.jpeg");
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  font-family: 'Open Sans', sans-serif;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: var(--white)
}

a {
  color: var(--secondary-color);
  text-decoration: none;
}

/* Generic Heading Styles */
h1, h2, h3, h4 {
  justify-content: center;
  font-family: 'Open Sans', sans-serif;
  font-size: 48px;
  color: var(--black);
  margin-top: 10px;
  margin-bottom: 10px;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

.page-title {
  text-align: center;
  width: 100%;
  margin: 0;
  font-size: 2.5rem;
}

/* Header Styles */

header h1 {
  padding-left: 25px;
  justify-content: center;
}

/* Navigation Styles */
nav {
  display: flex;
  justify-content: center;
  color: var(--black);
  background-position: 80%;
  background-size: cover;
  background-attachment: fixed;
  padding-top: 15px;
  flex-wrap: wrap;
}

nav ul {
  margin: 10px 20px 10px 20px;
  display: flex;
  flex-wrap: wrap;
}

nav ul li {
  display: inline;
  list-style-type: none;
  /* flex-basis: 200px; */
  padding: 15px 20px;
  opacity: 100%;
}

ul li a {
  text-decoration: none;
  opacity: 100%;
  padding: 10px;
  display: block;
}

nav ul li a {
  color: var(--black)
}

/* LAYOUT */
.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.container-fluid {
  width: 92%;
}

/* DISPLAY */
.display-none {
  display: none;
}

.display-block {
  display: block;
}

.display-inline-block {
  display: inline-block;
}

.display-inline {
  display: inline;
}

/* main content section styles */
.main-content-wrapper {
  width: 85%;
}

.main-content-wrapper {
  padding-top: 15px;
  padding-bottom: 25px;
  margin: 0 auto;
}

/* Form */
button {
  font-family: 'Open Sans', sans-serif;
}

.main {
  background-color: rgba(250, 235, 215, .9);
  padding: 25px;
  border-radius: 8px;
}

.main h2 {
  color: var(--primary-color);
  margin-top: 15px;
}

/* FORM */
.form-label,
.form-input,
.form-textarea {
  display: block;
}

.form-label {
  font-size: 1.2rem;
  margin: 0.625rem 0 0.25rem;
}

.form-input {
  width: 100%;
  padding: 0.75rem;
  margin: 0.2rem 0;
  font-size: 1.2rem;
  border: 2px solid var(--dark);
  border-radius: var(--border-radius);
}

[type='radio']:checked,
[type='radio']:not(:checked) {
  position: absolute;
  left: -9999px;
}
[type='radio']:checked + label,
[type='radio']:not(:checked) + label {
  position: relative;
  padding-left: 25px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  color: var(--secondary);
  margin-right: 0.2rem;
}

[type='radio']:checked + label:before,
[type='radio']:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  border: 2px solid var(--secondary);
  border-radius: 100%;
  background: #fff;
}
[type='radio']:checked + label:after,
[type='radio']:not(:checked) + label:after {
  content: '';
  width: 12px;
  height: 12px;
  background-color: var(--secondary);
  position: absolute;
  top: 5px;
  left: 5px;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

[type='radio']:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}

[type='radio']:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

/* CARDS */
.card {
  justify-content: center;
  margin: 0 0 20px 0;
  border: 3px solid var(--dark);
  border-radius: var(--border-radius);
  background-color: var(--light);
  color: var(--dark);
}

.card-header {
  color: var(--secondary);
  margin: 0;
  background-color: var(--dark);
  padding: 25px;
  border-radius: 8px;
}

.card-body {
  padding: 2.5%;
  font-size: 1.4rem;
}

/* Navigation Styles */
nav {
  display: flex;
  justify-content: flex-start;
  color: var(--black);
  background-position: 80%;
  background-size: cover;
  background-attachment: fixed;
  padding-top: 15px;
  flex-wrap: wrap;
}

nav ul {
  margin: 10px 20px 10px 20px;
  display: flex;
  flex-wrap: wrap;
}

nav ul li {
  display: inline;
  list-style-type: none;
  /* flex-basis: 200px; */
  padding: 15px 20px;
  opacity: 100%;
}

ul li a {
  text-decoration: none;
  opacity: 100%;
  padding: 10px;
  display: block;
}

nav ul li a {
  color: var(--black)
}

/* Styles for the footer */

footer {
  width: 100%;
  padding: 30px 0;
  display: flex;
  justify-content: space-around;
  height: 50px;
}